.shop-layout {
  display: flex;
  gap: 20px;
  padding: 20px;
}

/* ===== FILTER PANEL ===== */
.filters {
  width: 260px;
  background: #ffffff;
  border-radius: 12px;
  padding: 16px;
  box-shadow: 0 4px 14px rgba(0,0,0,0.08);
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.filters h3 {
  margin-bottom: 8px;
}

.filters input,
.filters select {
  padding: 10px;
  border-radius: 8px;
  border: 1px solid #ddd;
  font-size: 14px;
}

.clear-btn {
  background: #111827;
  color: white;
  padding: 10px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
}

/* ===== PRODUCTS ===== */
.products {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  gap: 20px;
}

.product-card {
  background: white;
  border-radius: 12px;
  padding: 12px;
  box-shadow: 0 4px 14px rgba(0,0,0,0.08);
  text-align: center;
}

.product-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 10px;
}

.product-card h4 {
  margin: 10px 0 4px;
}

.price {
  font-weight: bold;
  color: #16a34a;
}

.meta {
  font-size: 12px;
  color: #6b7280;
}

.product-card button {
  margin-top: 10px;
  width: 100%;
  padding: 10px;
  border-radius: 8px;
  background: #2563eb;
  color: white;
  border: none;
  cursor: pointer;
}

.empty {
  grid-column: 1/-1;
  text-align: center;
  color: gray;
}
