.checkout-wrapper {
  max-width: 1100px;
  margin: 30px auto;
  padding: 20px;
}

.checkout-title {
  text-align: center;
  font-size: 28px;
  font-weight: 800;
  margin-bottom: 24px;
}

.checkout-grid {
  display: grid;
  grid-template-columns: 1.2fr 1fr;
  gap: 24px;
}

.checkout-card {
  background: #fff;
  padding: 20px;
  border-radius: 14px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
}

.checkout-card h3 {
  margin-bottom: 16px;
}

.checkout-card input,
.checkout-card textarea {
  width: 100%;
  padding: 10px 12px;
  margin-bottom: 12px;
  border-radius: 8px;
  border: 1px solid #d1d5db;
  font-size: 14px;
}

.row {
  display: flex;
  gap: 10px;
}

.cod-box {
  margin-top: 10px;
  background: #ecfeff;
  padding: 10px;
  border-radius: 8px;
  font-weight: 600;
  color: #0369a1;
}

.summary-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.summary-total {
  display: flex;
  justify-content: space-between;
  font-size: 18px;
  font-weight: 800;
  margin-top: 10px;
}

.place-btn {
  width: 100%;
  margin-top: 16px;
  padding: 12px;
  background: #16a34a;
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
}

.place-btn:disabled {
  background: #9ca3af;
  cursor: not-allowed;
}

@media (max-width: 900px) {
  .checkout-grid {
    grid-template-columns: 1fr;
  }
}
